// TODO: Refactor this if possible
/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export class PopupDeferred extends Promise<any> {
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  resolve: (result?: any) => void;
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  reject: (reason?: any) => void;

  constructor(_beforeResolve: Function, _beforeReject: Function) {
    let _resolveSelf;
    let _rejectSelf;

    super((resolve, reject) => {
      /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
      _resolveSelf = (result?: any) => {
        _beforeResolve();
        resolve(result);
      };
      /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
      _rejectSelf = (reason?: any) => {
        _beforeReject();
        reject(reason);
      };
    });

    this.resolve = _resolveSelf;
    this.reject = _rejectSelf;
  }
}
